.App {
  text-align: center;
}

.Box {
  text-align: center;
  border-bottom: solid 1px #dbdbdb;
  padding-bottom: 20px;
}
input::placeholder {
  color: #dbdbdb;
}

.inputBox {
  width: 300px;
  margin-right: 1.3em;
  padding: 0.4em;
  font-size: 16px;
  border-bottom: none;
  border-left: none;
  border-right: none;
  border-top: none;
  background: transparent;
}

.addBtn {
  color: white; 
  display: inline-block;
  border: 1px solid rgba(0,0,0,0.21);
  border-bottom-color: rgba(0,0,0,0.34);
  text-shadow:0 1px 0 rgba(0,0,0,0.15);
  box-shadow: 0 1px 0 rgba(255,255,255,0.34) inset, 
                          0 2px 0 -1px rgba(0,0,0,0.13), 
                          0 3px 0 -1px rgba(0,0,0,0.08), 
                          0 3px 13px -1px rgba(0,0,0,0.21);
  width : 35px;
  height : 30px;
  padding: 0.4em;
  background: #47996b;
  margin: 0.5em 0.3em;
  border: none;
  font-size: 18px;
  border-radius: 0.6em;
}

.todoItemIdx {
  float: left;
  width: 20%;
}

.todoItemDesc {
  float: left;
  width: 60%;
}
.todoItemDel {
  float: left;
  width: 20%;
}